.Wallpaper {
  z-index: -10;
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  transition: background-color 1s ease-in-out;
  animation: fade-in 1s ease-in-out both;
}

.Wallpaper .pixelated {
  image-rendering: pixelated;
}

.Dark .Wallpaper {
  background-color: rgba(0, 0, 0, 0.8);
}

@keyframes fade-in {
  from { opacity: 0; }
  to {opacity: 1; }
}
